import React, { useEffect, useState } from 'react';

import { Header, LinkContainer, Section, Link } from '../styles/Banner';

const Banner = () => {

  const [isScrolled, setIsScrolled] = useState(true);
  const fadeIn = () => { 
    setIsScrolled(window?.pageYOffset < 75);
  }
  useEffect(() => {
    document.addEventListener('scroll', fadeIn);
  });

  return (
    <Header fadeIn={isScrolled}>
      <LinkContainer>
        <Section/>
        <Section>
          <Link>About Me</Link>
          <Link>Work Experience</Link>
          <Link>Resume</Link>
        </Section>
      </LinkContainer>
    </Header>
  )
}

export default Banner;
